.map-big {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  width: 224px;
  height: fit-content;
  overflow: hidden;
  box-sizing: border-box;
  background-color: black;
  position: relative;
  box-shadow: 2px 2px 8px 2px rgba(0, 0, 0, 0.3);
  border: 0px solid white;
  transition: all 0.2s linear;

  &:hover &__logo {
    scale: 1.02;
  }

  &:hover &__logo_checked {
    scale: 1.2;
  }
  
  &_checked {
    border: 7px solid white;
    box-shadow: 0px 0px 4px 1px rgba(255, 255, 255, 1);
  }

  &_checked &__logo {
    scale: 1.2;
    opacity: 1;
  }

  &_checked  &__list {
    opacity: 1;
  }

  &_checked  &__title {
    opacity: 1;
  }

  &__logo {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    display: flex;
    position: absolute;
    box-sizing: border-box;
    object-fit: cover;
    object-position: center;
    opacity: 0.5;
    transition: all 0.2s linear;
    z-index: 0;
    //animation: soft-logo-switch 5s linear infinite;
    //animation-delay: 2.5s;

    &_back {
    }
    

    &_anim {
      animation: soft-logo-switch 3s linear infinite;
    }

    &_checked-anim {
      animation: soft-logo-checked-switch 3s linear infinite;
    }
  }
  
  &__list {
    margin: 0;
    padding: 10px;
    width: 100%;
    display: flex;
    gap: 12px;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
    opacity: 0.8;
  }

  &__item {
    margin: 0;
    padding: 0;
    display: flex;
    position: relative;
    width: 70px;
    height: 70px;
    opacity: 0.8;

  }

  &__icon {
    margin: 0;
    padding: 0;
    display: flex;
    width: 100%;
    height: 100%;    
  }

  &__title {
    margin: 0;
    padding: 16px 0;
    width: 100%;
    display: flex;
    justify-content: center;
    text-align: center;
    font-size: 20px;
    font-weight: 400;
    color: white;
    letter-spacing: 1px;
    z-index: 1;
    opacity: 0.8;
  }

  &__checkbox {
    margin: 0;
    padding: 0;
    width: 24px;
    height: 24px;
    box-sizing: border-box;
    display: flex;
    border: 2px solid white;
    border-radius: 5px;
    position: absolute;
    top: 8px;
    right: 8px;
    opacity: 0.8;

    &_visible {
      background-image: url(../images/map_checkbox_icon.png);
      background-repeat: no-repeat;
      background-position: center;
      opacity: 1;
    }

    &_unvisible {
      opacity: 0;
    }
  }

  
}
 

@keyframes soft-logo-switch {
  from {
    opacity: 0.5;
  }
  50% {
    opacity: 0;
  }
  to {
    opacity: 0.5;
  }
}

@keyframes soft-logo-checked-switch {
  from {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
